.carousel-inner, .carousel-item {
    height: 100%;
}

.carousel-indicators {
    margin-bottom: 5rem;
}

.carousel-caption {
    bottom: 25%;
    left: 0;
    right: 0;
    width: 100%;
    height: max(40%, 20rem);
    background: rgba(86, 92, 99, 0.616);
    backdrop-filter: blur(1rem);
    cursor: pointer;
}

.default-carousel-img {
    object-position: right bottom;
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.carousel {
    z-index: 0;
}

.carousel-caption h3 {
    font-size: 240%;
    user-select: none;
}

.carousel-caption p {
    font-size: 120%;
    user-select: none;
}

.carousel-control-prev, .carousel-control-next {
    width: 5%;
}

